// ** Custom Components
import Avatar from '@components/avatar'
// ** Icons Imports
import { Check, File, MoreVertical } from 'react-feather'
// ** Reactstrap Imports
import { Card, CardHeader, CardTitle, CardBody, Row, Col } from 'reactstrap'
import { useEffect, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'

import moment from 'moment'
import { activeOrganization, orgUserId } from '@src/helper/sassHelper'
import { useNavigate } from 'react-router-dom'
const userId = orgUserId()
const activeOrg = activeOrganization()

const MyOverdueTasks = () => {

  const endDate = moment().startOf('day').unix()

  const dueDays = (val) => {
    const date = moment()
    let due = ''
    if (val.length === 13) {
      due = moment(val, 'x')
    } else {
      due = moment.unix(val)
    }

    return date.diff(due, 'days')
  }
  const navigate = useNavigate()
  const [data, setData] = useState([])
  const getTasks = () => {
    axios.post('/tasks/dashboardmytaskoverdues', { userId, endDate, userType: activeOrg.organizationusersusertypeid })
      .then((res) => {
        setData(res.data.tasks)
      })
      .catch((err) => { console.log(err) })
  }

  useEffect(() => {
    if (activeOrg !== null) {
      getTasks()
    }
  }, [])

  const updateParam = async (sts) => {
    const obj = {
      status: sts
    }
    await dispatch(updateStatusParams(obj))
    navigate('/task/list')
  }


  const renderTransactions = () => {
    return data.map((item, k) => {
      if (k < 5) {
        return (
          <div key={item.id} className='transaction-item'>
            <div className='d-flex'>
              <Avatar className='rounded' size={'sm'} color={'light-primary'} icon={<Check size={12} />} />
              <div>
                <h6 className='transaction-title'>{item.servicename}</h6>
                <small>{item.clientname}</small>
              </div>
            </div>
            <div className={`fw-bolder text-danger`}>{dueDays(item.enddate)} Day's</div>
          </div>
        )
      }
    })
  }

  return (
    <Card className='card-transaction'>
      <CardHeader>
        <CardTitle tag='h4'>
          <MoreVertical size={18} />
          My OverDue Tasks</CardTitle>
      </CardHeader>
      <CardBody>
        {data.length > 0 &&
          renderTransactions()
        }
        {
          data.length === 0 &&
          <Row>
            <File />
            <p className='text-center mt-2'>You Dont have any Overdue tasks.</p>
          </Row>
        }
        {
          data.length > 4 &&
          (<Col onClick={() => { updateParam(item.status) }} className='cursor-pointer text-primary'>View More...</Col>)
        }
      </CardBody>
    </Card>
  )
}

export default MyOverdueTasks
