// ** Third Party Components
import classnames from 'classnames'
import { TrendingUp } from 'react-feather'

// ** Custom Components
import Avatar from '@components/avatar'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import { Card, CardHeader, CardTitle, CardBody, CardText, Row, Col } from 'reactstrap'
import { useEffect, useState } from 'react'
import { activeOrganization, orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
import { updateStatusParams } from '@src/views/apps/task/store/index.js'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
const userId = orgUserId()
const activeOrg = activeOrganization()
const data = [
  {
    status: 1,
    title: '0',
    subtitle: 'Assigned',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 2,
    title: '0',
    subtitle: 'Inprogress',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 3,
    title: '0',
    subtitle: 'Completed',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 6,
    title: '0',
    subtitle: 'Pending Review',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 7,
    title: '0',
    subtitle: 'Request Changes',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 8,
    title: '0',
    subtitle: 'Overdue',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  }
]

const StatsCard = ({ cols }) => {


  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [dataArr, setData] = useState([])

  const getStats = () => {

    const obj = { date: moment().startOf('day').utc().format('x'), userId, userType: activeOrg.organizationusersusertypeid }
    axios.post('tasks/taskcountbystatus', obj)
      .then((res) => {
        data.forEach((obj, key) => {
          const ind = res.data.tasks.findIndex((o) => parseInt(o.taskstatus) === obj.status)
          if (ind > -1) {
            data[key].title = res.data.tasks[ind].count
          }
        })
        setData(data)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  const updateParam = async (sts) => {
    const obj = {
      status: sts
    }
    await dispatch(updateStatusParams(obj))
    navigate('/task/list')
  }


  useEffect(() => {
    if (activeOrg !== null) {
      getStats()
    }
  }, [])

  const renderData = () => {
    return dataArr.map((item, index) => {

      return (
        <Col
          key={index}
          {...cols}
          className={classnames({
            [`mb-2 `]: index !== data.length - 1
          })}
        >
          <div className='d-flex align-items-center cursor-pointer' onClick={() => { updateParam([item.status]) }}>
            <Avatar color={item.color} icon={item.icon} className='me-2' />
            <div className='my-auto'>
              <h6 className='fw-bolder mb-0'>{item.title}</h6>
              <CardText className='font-small-3 mb-0'>{item.subtitle}</CardText>
            </div>
          </div>
        </Col>
      )
    })
  }

  return (
    <Card className='card-statistics'>
      <CardHeader>
        <CardTitle tag='h4'>Statistics</CardTitle>
        {/* <CardText className='card-text font-small-2 me-25 mb-0'>Updated 1 month ago</CardText> */}
      </CardHeader>
      <CardBody className='statistics-body'>
        <Row>{renderData()}</Row>
      </CardBody>
    </Card>
  )
}

export default StatsCard
