// ** React Imports
//import { useContext } from 'react'

// ** Reactstrap Imports
import { Row, Col, Card, CardBody } from 'reactstrap'

// ** Context
//import { ThemeColors } from '@src/utility/context/ThemeColors'

// ** Demo Components
import CardAttendance from '../../ui-elements/cards/advance/CardAttendance'
import StatsCard from '@src/views/ui-elements/cards/statistics/StatsCard'
import MyTaskTodays from '@src/views/ui-elements/cards/advance/MyTaskTodays'
import MyTaskDueTodays from '@src/views/ui-elements/cards/advance/MyTaskDueTodays'
import MyOverdueTasks from '@src/views/ui-elements/cards/advance/MyOverdueTasks'
import CardEmployeesTask from '@src/views/ui-elements/cards/advance/CardEmployeesTask'


// ** Styles
import axios from '@src/configs/axios/axiosConfig'
import '@styles/react/libs/charts/apex-charts.scss'
import '@styles/base/pages/dashboard-ecommerce.scss'
import { useEffect, useState } from 'react'
import { CheckCircle, Circle, XCircle } from 'react-feather'
import { Link } from 'react-router-dom'
import moment from 'moment'

const EcommerceDashboard = () => {

  const [settingFlags, setSettingFlags] = useState({ gmail_flag: false, leave_flag: false, drive_flag: false, attendance_flag: false, invoicesetting_flag: false })
  const [events, setEvents] = useState([])
  const getSettingFlag = () => {
    axios.post('/organizationprefernces/getsettingsflag')
      .then((res) => {
        setSettingFlags(res.data.result)
      }).catch((err) => {
        console.log(err)
      })
  }

  const getTodayEvents = () => {
    const date = moment().startOf('day').unix()
    axios.post('/organizations/gettodayevents', { date })
      .then((res) => {
        setEvents(res.data.events)        
      }).catch((err) => {
        console.log(err)
      })
  }

  console.log(events)

  useEffect(async () => {
    await getSettingFlag()

    if (!settingFlags['gmail_flag'] && !settingFlags['leave_flag'] && !settingFlags['drive_flag'] && !settingFlags['attendance_flag'] && !settingFlags['invoicesetting_flag']) {
      await getTodayEvents()
    }
  }, [])

  return (
    <div id='dashboard-ecommerce'>
      <Row className='match-height'>
        <Col xl='8' md='6' xs='12'>
          <Row>
            <Col>
              <CardAttendance />
            </Col>

            {
              (settingFlags['gmail_flag'] || settingFlags['leave_flag'] || settingFlags['drive_flag'] || settingFlags['attendance_flag'] || settingFlags['invoicesetting_flag']) &&
              <Col>
                <Card xl='4' md='6' xs='12'>
                  <CardBody >
                    <Row className='mb-1'>
                      <Col>
                        {
                          settingFlags['gmail_flag'] ? <Circle className='text-danger' /> : <CheckCircle className='text-success' />
                        }
                        <Link to={'/account-settings/integration'} className='ms-md-50'>Link gmail</Link>
                      </Col>
                    </Row>
                    <Row className='mb-1'>
                      <Col>
                        {
                          settingFlags['drive_flag'] ? <Circle className='text-danger' /> : <CheckCircle className='text-success' />
                        }
                        <Link to={'/account-settings/integration'} className='ms-md-50'>Link Google Drive for Storage</Link>
                      </Col>
                    </Row>
                    <Row className='mb-1'>
                      <Col>
                        {
                          settingFlags['attendance_flag'] ? <Circle className='text-danger' /> : <CheckCircle className='text-success' />
                        }
                        <Link to={'/account-settings/attendancesetting/businesshours'} className='ms-md-50'>Set Buiness Hours</Link>
                      </Col>
                    </Row>
                    <Row className='mb-1'>
                      <Col>
                        {
                          settingFlags['leave_flag'] ? <Circle className='text-danger' /> : <CheckCircle className='text-success' />
                        }
                        <Link to={'/account-settings/attendancesetting/leavesetting'} className='ms-md-50'>Enable Leave</Link>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        {
                          settingFlags['invoicesetting_flag'] ? <Circle className='text-danger' /> : <CheckCircle className='text-success' />
                        }
                        <Link to={'/account-settings/invoicesetting/invoiceaccount'} className='ms-md-50'>Invoice Settings</Link>
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            }
            {(!settingFlags['gmail_flag'] && !settingFlags['leave_flag'] && !settingFlags['drive_flag'] && !settingFlags['attendance_flag'] && !settingFlags['invoicesetting_flag']) &&
              <Col>
                <Card xl='4' md='6' xs='12'>
                  <CardBody>
                    <h6> Today Highlights</h6>

                  </CardBody>
                </Card>
              </Col>
            }
          </Row>
          <StatsCard cols={{ xl: '3', sm: '6' }} />
        </Col>
        <Col xl='4' md='6' xs='12'>
          <CardEmployeesTask />
        </Col>
      </Row>
      <Row className='match-height'>
        <Col xl='4' md='6' xs='12'>
          <MyTaskTodays />
        </Col>
        <Col xl='4' md='6' xs='12'>
          <MyTaskDueTodays cols={{ xl: '2', sm: '6' }} />
        </Col>
        <Col xl='4' md='6' xs='12'>
          <MyOverdueTasks cols={{ xl: '2', sm: '6' }} />
        </Col>
      </Row>
    </div >
  )
}

export default EcommerceDashboard
