// ** Custom Components
import Avatar from '@components/avatar'
import { useEffect, useState } from 'react'

// ** Third Party Components
import Chart from 'react-apexcharts'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import { Card, CardHeader, CardTitle, CardBody } from 'reactstrap'
import moment from 'moment'
import { Link } from 'react-router-dom'

const CardEmployeesTasks = () => {

  const [employeesTasks, setEmployeeTasks] = useState([])

  const taskTime = (val) => {
    if (val === null) {
      return '-'
    }
    return moment.unix(val).format('MMM DD hh:mm')
  }

  const renderTasks = () => {
    return employeesTasks.map((task, k) => {
      return (
        <div key={k} className='employee-task d-flex justify-content-between align-items-center'>
          <div className='d-flex'>
            <Avatar initials color={'light-primary'} className='me-75' content={task.username?.charAt(0).toUpperCase()} />
            <div className='my-auto'>
              <h6 className='mb-0'>{task.username}</h6>
              {task.taskid !== null && (
                <small>
                  <Link to={`/tasks/view/${task.taskid}`}>{task.servicename}</Link></small>
              )
              }
            </div>
          </div>
          <div className='d-flex align-items-center'>
            <small className='text-muted me-75'>{taskTime(task.starttime)}</small>
          </div>
        </div>
      )
    })
  }

  const getCurrentTask = () => {

    axios.post('/tasks/getcurrenttasks')
      .then((res) => {
        setEmployeeTasks(res.data.users)
      }).catch((err) => {
        console.log(err)
      })

  }

  useEffect(() => {
    getCurrentTask()
  }, [])

  return (
    <Card className='card-employee-task'>
      <CardHeader>
        <CardTitle tag='h4'>Live Task</CardTitle>
      </CardHeader>
      <CardBody>{renderTasks()}</CardBody>
    </Card>
  )
}

export default CardEmployeesTasks
