// ** Custom Components
import Avatar from '@components/avatar'
import { Link } from 'react-router-dom'
// ** Icons Imports
import { Check, File, MoreVertical } from 'react-feather'
// ** Reactstrap Imports
import { Card, CardHeader, CardTitle, CardBody, Row, Col } from 'reactstrap'
import { useEffect, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'

import moment from 'moment'
import { activeOrganization, orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
const activeOrg = activeOrganization()

const MyTaskDueTodays = () => {

  const dateFormat = (value) => {

    return moment.unix(value).format("MMM DD, YYYY")

  }

  const endDate = moment().startOf('day').unix()

  const [data, setData] = useState([])
  const getTasks = () => {
    axios.post('/tasks/dashboardmytasklisttoday', { userId, endDate, userType: activeOrg.organizationusersusertypeid })
      .then((res) => {
        setData(res.data.tasks)
      })
      .catch((err) => { console.log(err) })
  }

  useEffect(() => {
    if (activeOrg !== null) {
      getTasks()
    }
  }, [])

  const renderTransactions = () => {
    return data.map((item, k) => {
      if (k < 5) {
        return (
          <div key={item.id} className='transaction-item'>
            <div className='d-flex'>
              <Avatar className='rounded' size={'sm'} color={'light-primary'} icon={<Check size={12} />} />
              <div>
                <h6 className='transaction-title'>{item.servicename}</h6>
                <small>{item.clientname}</small>
              </div>
            </div>
            <div className={`fw-bolder text-success`}>{dateFormat(item.enddate)}</div>
          </div>
        )
      }
    })
  }

  return (
    <Card className='card-transaction'>
      <CardHeader>
        <CardTitle tag='h4'>
          <MoreVertical size={18} />
          My Task Dues Today </CardTitle>
      </CardHeader>
      <CardBody>
        {data.length > 0 &&
          renderTransactions()
        }
        {
          data.length === 0 &&
          <Row>
            <File />
            <p className='text-center mt-2'>You Dont have any tasks dues Today</p>
          </Row>
        }
        {
          data.length > 4 &&
          (<Col tag={Link} to={`/task/list`} className='cursor-pointer text-primary'>View More...</Col>)
        }
      </CardBody>
    </Card>
  )
}

export default MyTaskDueTodays
